  font-size: 20px;
}
ul.header-icons li a:hover, .footer ul.header-icons li address:hover {
  color: #F1C40F;
}

.box {
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  left: 50%;
}
.box .input {
  padding: 10px;
  width: 40px;
  height: 30px;
  background: none;
  border: none;
  border-radius: 50px;
  box-sizing: border-box;
  font-size: 20px;
  color: #F1C40F;
  outline: none;
  transition: 0.5s;
}
.box .input:hover {
  width: 245px;
  background: #ffffff;
  border-radius: 10px;
}
.box i {
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  right: 15px;
  font-size: 20px;
  color: #ffffff;
  transition: 0.2s;
}
.box i:hover {
  opacity: 0;
  z-index: -1;
}

/*footer*/
.footer {
  background-color: #000000;
  padding: 50px 0;
}
.footer img {
