  color: #F1C40F;
  padding: 10px 0;
  margin: 0;
  margin-bottom: 20px;
}

* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

body {
  background-color: #ffffff;
  color: #707070;
  font-size: 15px;
  font-family: "Arial", sans-serif;
}

ul li {
  list-style: none;
  display: inline-block;
}

img {
  display: inline-block;
  width: 100%;
}

.pts-5 {
  padding-top: 10px;
}

.pt-10 {
  padding: 10px;
}

/*Hero Section */
.hero .carousel-item {
  height: 100vh;
  min-height: 350px;
  background: no-repeat center center scroll;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}
.hero .carousel-item .btn-primary {
  background-color: #ffffff;
  border: 1px solid #ffffff;
  color: #F1C40F;
  padding: 5px 20px;
}
.hero .carousel-item .btn-primary:hover {
  color: #F1C40F;
  background-color: #000000;
  border: 1px solid #000000;
}
.hero .carousel-item .carousel-caption {
  text-align: right;
  width: 25%;
  left: unset;
  top: 50%;
  bottom: unset;
  transform: perspective(1px) translateY(-50%);
  z-index: 1;
}
.hero .carousel-item .carousel-caption h3 {
  font-size: 40px;
}
.hero .carousel-item .carousel-caption p {
  color: #ffffff;
  font-size: 18px;
  line-height: 25px;
}
.hero .drk::after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #000000;
  opacity: 0.4;
}

/*hot-deals */
.hot-deals {
  text-align: center;
  padding: 50px 0;
}
.hot-deals .deals-product {
  position: relative;
  min-width: 200px;
  margin: 0 auto;
}
.hot-deals .deals-product::after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #000000;
  opacity: 0.4;
}
.hot-deals .deals-product .deals-heading {
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  left: 50%;
  width: 80%;
  border: 1px solid #ffffff;
  padding: 6rem 0.1rem;
  z-index: 1;
}
.hot-deals .deals-product .deals-heading h5 {
  color: #ffffff;
}
.hot-deals .deals-product .deals-heading .btn-primary {
  background-color: transparent;
  border: 1px solid #ffffff;
}
.hot-deals .deals-product .deals-heading .btn-primary:hover {
  box-shadow: none;
  color: #F1C40F;
  border: 1px solid #F1C40F;
}
.hot-deals .deals-product .deals-heading .btn-primary:active:focus {
  box-shadow: none;
  color: #F1C40F;
  border: 1px solid #F1C40F;
}

.btn-check:focus + .btn-primary, .btn-primary:focus {
  box-shadow: 0 0 0 0.25rem #F1C40F;
}

/* Our Products*/
.our-products {
