/* sidebar menu */
.sidebar-menu {
  margin: 30px 0;
}
.sidebar-menu ul.accordion {
  list-style-type: none;
  border: 1px solid #000000;
  padding-left: 0;
  margin-top: 0;
}
.sidebar-menu ul.accordion li {
  display: block;
}
.sidebar-menu ul.accordion li:last-child .link {
  border-bottom: 0;
}
.sidebar-menu ul.accordion span {
  text-transform: uppercase;
}
.sidebar-menu ul.accordion a, .sidebar-menu .footer ul.accordion address, .footer .sidebar-menu ul.accordion address {
  color: #000000;
  text-decoration: none;
}
.sidebar-menu .accordion {
  width: 100%;
  max-width: 384px;
  margin: 30px auto 20px;
  background: #000000;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  border-radius: 0px;
}
.sidebar-menu .accordion .link {
  cursor: pointer;
  display: block;
  padding: 15px 15px 15px 42px;
  color: #ffffff;
  font-size: 14px;
  font-weight: 700;
  border-bottom: 1px solid #ffffff;
  position: relative;
  -webkit-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
}
.sidebar-menu .accordion li i {
  position: absolute;
  top: 16px;
  left: 12px;
  font-size: 18px;
  color: #ffffff;
  -webkit-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
}
.sidebar-menu .accordion li i.fa-chevron-down {
  right: 12px;
  left: auto;
  font-size: 16px;
}
.sidebar-menu .accordion .open .link {
  color: #ffffff;
  background-color: #F1C40F;
  border: 1px solid #F1C40F;
}
.sidebar-menu .accordion .open i {
  color: #ffffff;
}
.sidebar-menu .accordion .open i.fa-chevron-down {
  -webkit-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  -o-transform: rotate(180deg);
  transform: rotate(180deg);
}
.sidebar-menu .accordion .submenu {
  display: none;
  background: #ffffff;
  font-size: 14px;
  padding-left: 0;
}
.sidebar-menu .accordion .submenu li {
  border-bottom: 1px solid #000000;
}
.sidebar-menu .accordion .submenu a, .sidebar-menu .accordion .submenu .footer ul address, .footer ul .sidebar-menu .accordion .submenu address {
  display: block;
  padding: 12px;
  text-align: center;
  -webkit-transition: all 0.25s ease;
  -o-transition: all 0.25s ease;
  transition: all 0.25s ease;
}
.sidebar-menu .accordion .submenu a:hover, .sidebar-menu .accordion .submenu .footer ul address:hover, .footer ul .sidebar-menu .accordion .submenu address:hover {
  background: #F1C40F;
  color: #ffffff;
}
.sidebar-menu .product-overlay-icon {
  top: 50%;
  left: 0;
  width: 100%;
  padding: 10px;
}
.sidebar-menu .product-overlay-icon a, .sidebar-menu .product-overlay-icon .footer ul address, .footer ul .sidebar-menu .product-overlay-icon address {
  padding: 10px;
}

